<template>
	<view class="content">
		<view class="top_bg">
			<!-- 头部导航 -->
			<view class="top_img">
				<image src="../../../static/img/store/left.png" mode="" @tap="black"></image>
				<text>我的店铺</text>
			</view>
			<!-- 搜索条 -->
			<view class="search" @tap="search">
				<image src="../../../static/img/our/store/search.png" mode=""></image>
			</view>
			<!-- 进度条-->
			<view class="bar">
				<view class="left">
					<view style="display: flex;justify-content: center;position: relative;">
						<cmd-progress type="dashboard" :percent="percent" stroke-color="#d5d5d5" :stroke-width="7" :width="120" stroke-shape="square"
						 :showInfo="false"></cmd-progress>
						<view style="position: absolute;text-align: center;top: 30%;">
							<view style="font-size:12px;">今日新增会员数</view>
							<view style="font-size:20px;padding-top: 10rpx;">{{data.TodayNewUser}}人</view>
						</view>
					</view>
					<view class="">
						剩余未消费金额
					</view>
					<view class="">
						{{data.TotalNextMoney}}
					</view>
				</view>
				<view class="right">
					<view class="right_li">
						<view class="">
							今日消费金额
						</view>
						<view class="" style="font-size: 14px;">
							{{data.TodayBUyMoney}}
						</view>
					</view>
					<view class="right_li">
						<view class="">
							今日充值金额
						</view>
						<view class="" style="font-size: 14px;">
							{{data.TodayRechargeMoney}}
						</view>
					</view>
					<view class="right_li">
						<view class="">
							今日消费订单
						</view>
						<view class="" style="font-size: 14px;">
							{{data.TodayBuyOrderNum}}单
						</view>
					</view>
					<view class="right_li">
						<view class="">
							今日充值订单
						</view>
						<view class="" style="font-size: 14px;">
							{{data.TodayRechargeNum}}单
						</view>
					</view>
				</view>
			</view>
			<view class="card_top">
				<view class="row">
				</view>
				<text>工具</text>
			</view>
		</view>
		<!-- 下面的菜单 -->
		<view class="tab_card">
			<view class="card_li" v-for="(item,index) in text_data" @tap='store_tab(item)'>
				<image :src="item.image" mode=""></image>
				<view class="text">
					{{item.text}}
				</view>
			</view>
		</view>
		<view class="fot">
			<view class="right">
				<image src="../../../static/img/our/store/dianpu.png" mode=""></image>
				<view style="color: #FA5F3E;">我的店铺</view>
			</view>
			<view class="center"  @tap='saoyisao'>
				<view class="er">
					<image src="../../../static/er.png" mode=""></image>
				</view>
				<!-- <image src="../../../static/img/our/store/saoyisao.png" mode=""></image> -->
				<view>门店二维码</view>
			</view>
			<view class="right" @tap='store_center'>
				<image src="../../../static/img/our/store/zhongx.png" mode=""></image>
				<view>门店中心</view>
			</view>
		</view>
	</view>
</template>

<script>
	import cmdProgress from "@/components/cmd-progress/cmd-progress.vue"
	/* 接口 */
	import {MyStore} from '../../../api/our/our.js'
	export default {
		data() {
			return {
				text_data: [{
						image: '../../../static/img/our/store/dingdan.png',
						text: '经营数据',
						url: '../Business_data/Business_data'
					},
					{
						image: '../../../static/img/our/store/shuju.png',
						text: '订单明细',
						url: '../order_details/order_details'
					},
					{
						image: '../../../static/img/our/store/haibao.png',
						text: '添加订单',
						url: '../order/order'
					}
				],
				data:{},//所有数据
				percent:null,//百分比
			}
		},
		/* 分享 */
		 onShareAppMessage(res){
			 
		 },
		onLoad() {
			this.getdata();
		},
		methods: {
			/* 页面跳转到门面中心 */
			store_center(){
				uni.navigateTo({
					url:'../store_center/store_center'
				})
			},
			/* 返回上一级 */
			black(){
				uni.navigateBack({
					delta:1
				})
			},
			/* 跳转到搜索 */
			search(){
				uni.navigateTo({
					url:'../search/search'
				})
			},
			/* 小卡片跳转 */
			store_tab(item){
				uni.navigateTo({
					url:item.url
				})
			},
			/* 扫一扫 */
			saoyisao(){
				// 允许从相机和相册扫码
				// uni.scanCode({
				//     success: (res)=>{
				// 		uni.showToast({
				// 			title:res.result,
							
				// 		})
				//         console.log('条码类型：' + res.scanType);
				//         console.log('条码内容：' + res.result);
				//     }
				// });
				uni.navigateTo({
					url:'../poster/poster'
				})
			},
			/* 获取到数据 */
			getdata(){
				MyStore({
					token:uni.getStorageSync('token')
				}).then(res=>{
					if(res.code==1){
						this.data=res.data;
						this.percent=res.data.TodayNewUser/100;
						uni.setStorageSync('store_id',res.data.store_id);
					} else if(res.code==0){
						uni.showToast({
							title:res.msg,
							icon:'none',
							success: () => {
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1000)
							}
						})
						
					}
					// else if(res.code==5){
					// 	uni.navigateTo({
					// 		url:'../../store/Store_entry/Store_entry'
					// 	})
					// }
					else{
						uni.showToast({
							title:res.msg,
							icon:'none'
						})
					}
				})
			},

		},
		components: {
			cmdProgress
		}
	}
</script>

<style lang="scss">
	.content {
		.top_bg {
			background: url(data:image/png;base64,);
			background-repeat: no-repeat;
			background-size: 100% 100%;
			height: 650rpx;

			.top_img {
				width: 100%;
				display: flex;
				padding: 70rpx 0;
				font-size: 15px;
				color: #FFFFFF;

				text {
					text-align: center;
					margin-left: 35%;
				}

				image {
					width: 50rpx;
					height: 50rpx;
				}
			}

			.search {
				background-color: #FFFFFF;
				border-radius: 30rpx;
				margin: 0 20rpx;
				display: flex;
				align-items: center;

				image {
					width: 40rpx;
					height: 40rpx;
					line-height: 40rpx;
					margin-left: 20rpx;
					padding: 10rpx;
				}
			}

			.bar {
				display: flex;
				font-size: 14px;
				color: #FFFFFF;
				padding: 20rpx;

				.left {
					width: 40%;
					text-align: center;
				}

				.right {
					display: flex;
					flex-wrap: wrap;
					justify-content: center;

					.right_li {
						font-size: 12px;
						width: 35%;
						border-radius: 20rpx;
						background-color: rgba(255, 255, 255, .2);
						margin: 20rpx 10rpx;
						padding: 35rpx 25rpx;
						text-align: center;
					}
				}
			}

			.card_top {
				font-size: 16px;
				color: #333333;
				display: flex;
				align-items: center;
				font-weight: bold;
				padding: 30rpx;
				background-color: #FFFFFF;
				border-radius: 30rpx 30rpx 0 0;
				border-bottom: 1px solid #eee;

				.row {
					height: 20px;
					width: 2px;
					background-color: #FA5F3E;
				}

				text {
					padding-left: 20rpx;
				}
			}
		}

		.tab_card {
			display: flex;
			font-size: 14px;

			.card_li {
				text-align: center;
				margin-top: 150rpx;
				margin-left: 50rpx;

				image {
					width: 150rpx;
					height: 140rpx;
				}
			}
		}

		.fot {
			width: 100%;
			display: flex;
			justify-content: space-around;
			position: fixed;
			font-size: 12px;
			text-align: center;
			color: #6d6d6d;
			bottom: 0;
			padding: 20rpx 0;
			box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.07);

			.right {
				image {
					width: 50rpx;
					height: 50rpx;
				}
				view{
					padding-top: 5px;
				}
			}

			.center {
				margin-top: -30px;
				.er{
					width: 70rpx;
					height: 70rpx;
					background-color: #FA5F3E;
					border-radius: 50%;
					padding: 10rpx;
					display: flex;
					justify-content: center;
					align-items: center;
					box-shadow: 0px 0px 5px 5rpx #e2e2e2;
					margin-bottom: 25rpx;
					margin-left: 10rpx;
				}

				image {
					width: 50rpx;
					height: 50rpx;
				}
				view{
					padding-top: 5px;
				}
			}

		}
	}
</style>
